@mixin popup_base($width) {
    position: fixed;
    z-index: 9999;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: #{rgba(#fff, 0.8)};
    backdrop-filter: blur(5px);

    &-block {
        position: absolute;
        top: 50%;
        left: 50%;
        translate: -50% -50%;

        background-color: #fff;
        border-radius: 8px;
        border: 1px solid #e5e5e5;
        box-shadow: 0px 16px 48px 0px rgba(122, 106, 86, 0.1);

        width: $width * 1px;

        max-height: calc(100% - 10vh);
        padding: 32px;

        display: flex;
        flex-direction: column;
        gap: 32px;

    }
}